<template>
	<ul>
		<li @click="changeDifficulty(9,9,10)">ÂàùÁ∫ßÔºà9x9 10‰∏™Èõ∑Ôºâ</li>
		<li @click="changeDifficulty(16,16,40)">‰∏≠Á∫ßÔºà16x16 40‰∏™Èõ∑Ôºâ</li>
		<li @click="changeDifficulty(16,30,99)">È´òÁ∫ßÔºà16x30 99‰∏™Èõ∑Ôºâ</li>
		<li @click="showbySelf">Ëá™ÂÆö‰πâ</li>
	</ul>
	<ul v-if="show">
		<li><input v-model="rownum" placeholder="Âè™ËÉΩËæìÂÖ•Êï∞Â≠ó" type="text"> Ë°å</li>
		<li><input v-model="colnum" placeholder="Âè™ËÉΩËæìÂÖ•Êï∞Â≠ó" type="text"> Âàó</li>
		<li><input v-model="minenum" placeholder="Âè™ËÉΩËæìÂÖ•Êï∞Â≠ó" type="text"> Èõ∑</li>
		<button @click="submit">Á°ÆÂÆö</button>
	</ul>
</template>

<script>
	export default {
		name: 'difficulty',
		props: {
			// msg: String
		},
		data: function() {
			return {
				rownum:'',
				colnum:'',
				minenum:'',
				show:false
			}
		},
		methods: {
			changeDifficulty(row,col,mine) {
				this.$emit("difficulty",{
					rownum:row,
					colnum:col,
					minenum:mine
				})
			},
			showbySelf(){
				this.show=!this.show
			},
			submit(){
				this.changeDifficulty(this.rownum,this.colnum,this.minenum)
			}
		},
		watch: {
			rownum: function(val) {
				this.rownum = val.replace(/\D/g, '')
			},
			colnum: function(val) {
				this.colnum = val.replace(/\D/g, '')
			},
			minenum: function(val) {
				this.minenum = val.replace(/\D/g, '')
			}
		}

	}
</script>

